@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body,
.App {
  background-color: #faf9f9;
  color: #222 !important;
  margin-bottom: 80px;
}

body {
  overflow-x: hidden;
}

td,
th {
  text-align: left;
  padding: 4px 10px;
}

th {
  border: 1px solid rgba(0, 0, 0, 0.1);
}

tr:nth-child(even) {
  background-color: #f3f3f3;
}

a {
  text-decoration: none;
  color: inherit;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

img {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.errorMessage {
  animation: 0.1s linear ErrorPopUp;
}

#searchIconWrapper {
  cursor: pointer;
  padding: 0.9rem;
  background-color: #e9e8e8;
  border-radius: 5px;
}

#searchIconWrapper:hover #searchIcon {
  transform: scale(1.2);
}

@keyframes ErrorPopUp {
  from {
    transform: scale(0);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

.componentTransition {
  animation: componentTransition 0.4s;
}

.transition {
  animation-name: transition;
  animation-duration: 0.6s;
  overflow: hidden;
}

@keyframes transition {
  from {
    transform: translateX(30%);
    opacity: 0;
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes componentTransition {
  from {
    transform: scale(0);
    opacity: 0;
  }
  to {
    transform: scale(100%);
    opacity: 1;
  }
}

.categories {
  display: grid;
  align-items: center;
}

.shoppingCartNav {
  position: relative;
}

.NavBarContainer .MuiGrid-item > h3:hover,
.NavBarContainer .MuiGrid-item > h2:hover,
.MenuIcon:hover,
.shoppingCartIcon:hover {
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: #f50057;
}

/* Do not change the font color on element responsible for
 displaying the ammount of items in the shopping cart*/
.NavBarContainer .MuiGrid-item > p:hover {
  color: unset;
}

.shoppingCartNav p {
  background-color: #f50057;
  border-radius: 50%;
  padding: 1px 4px 1px 3px;
  text-align: center;
  position: absolute;
  right: -0.9rem;
  top: -0.1rem;
}

.shoppingCartIcon {
  position: absolute;
  left: -0.8rem;
  top: 30px;
}

.sideMenuContainer,
.MuiGrid-item {
  display: grid;
  place-items: center;
  margin-left: 20px;
}

.MenuIcon {
  margin: 20px;
  display: none !important;
  pointer-events: stroke;
}

.modalContainer {
  display: none;
  background-color: rgba(0, 0, 0, 0.4);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  color: rgba(0, 0, 0, 0.8);
}

.modalContent {
  display: grid;
  position: relative;
  right: 0;
  top: 0;
  background-color: #f1f1f1;
  place-items: center;
  justify-items: center;
  width: 60%;
  height: 100%;
  animation: slideLeft 0.1s linear;
}

.modalContent > ul {
  position: absolute;
  width: 100%;
  left: 0;
  margin-left: -40px;
  padding: 0;
  display: grid;
  text-align: center;
}
.modalContent > ul > li {
  list-style: none;
  padding: 20px;
  border: 1px solid rgba(0, 0, 0, 0.4);
  max-width: 100%;
  min-width: 100%;
  font-weight: 700;
}

@keyframes slideLeft {
  from {
    opacity: 0.5;
    transform: translateX(-100%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@media (max-width: 900px) {
  .MenuIcon {
    display: block !important;
    width: 100%;
    height: 100%;
    pointer-events: all;
    transform: scale(1.3);
  }
  .categories {
    display: none;
  }

  .navLogo {
    font-size: 22px;
  }

  .navbar-userSection > * {
    display: none;
  }

  .modalContent li:hover,
  .modalContent li:active {
    background-color: rgba(0, 0, 0, 0.08);
    color: #f50057;
    cursor: pointer;
  }
}

/* Hamburger menu */

.hamburger {
  display: none;
  color: white;
  font-size: 2rem;
  border-radius: 5px;
  width: 40px;
  height: 40px;
  position: relative;
  padding: 5px;
  z-index: 50;
}

.hamburger:hover {
  cursor: pointer;
}

.hamburger span {
  background-color: rgba(255, 255, 255, 0.9);
  width: 80%;
  height: 6px;
  border-radius: 5px;
  position: absolute;
  left: 10%;
  transform-origin: 3px 3px;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
    opacity 0.55s ease;
  z-index: 20;
}

.hamburger span:first-child {
  top: 20%;
}

.hamburger span:nth-child(2) {
  top: 43%;
}

.hamburger span:last-child {
  bottom: 20%;
}

.hamburger.active {
  transform: scale(1.1);
}

.hamburger.active span:first-child {
  transform: rotate(45deg);
}

.hamburger.active span:nth-child(2) {
  opacity: 0;
}

.hamburger.active span:last-child {
  transform: rotate(-45deg);
}

.menu-modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 10;
  background-color: rgb(46, 46, 46);
  overflow-y: hidden;
  overflow-x: hidden;

  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.menu-modal > ul {
  text-align: center;
}

.menu-modal > ul li {
  margin: 2rem 0;
  list-style: none;
}

.menu-modal > ul a {
  color: white;
  text-decoration: none;
}

/* Override any Material UI style */
.activeLink {
  font-weight: bold !important;
  font-size: 1.1rem !important;
  color: rgb(231, 36, 68) !important;
  text-decoration: none !important;
}

.NavBarContainer a {
  color: white;
  text-decoration: none;
}

.NavBarContainer a:hover {
  color: rgb(231, 36, 68);
}

/* Responsive view */

@media (max-width: 800px) {
  .hamburger {
    top: 20px;
    display: block;
  }

  .menu-modal {
    display: flex;
  }
}

@media (max-width: 800px) {
  .NavBarContainer {
    position: fixed;
    z-index: 5;
    bottom: 0;
  }

  .activeLink {
    font-weight: bold;
    font-size: 1.5rem;
    color: rgb(231, 36, 68) !important;
    text-decoration: none;
  }
}

.productsCarousel {
  overflow-x: scroll;
  scrollbar-width: none; /* Hide Scrollbar in firefox*/
  white-space: nowrap;
  display: flex;
  width: 100vw;
  max-height: 280px;
  margin-top: 30px;
  justify-content: center;
}

.productsCarousel::-webkit-scrollbar {
  display: none;
  background: transparent;
  width: 0;
}

.featured-card {
  min-width: 310px;
  min-height: 210px;
  cursor: pointer;
  margin: 10px;
  border-radius: 8px;
  box-shadow: 2px 6px 8px rgba(0, 0, 0, 0.2);
  position: relative;
}

.featured-card-image {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 0;
  border-radius: 8px;
}

.featured-card-badge {
  position: absolute;
  color: white;
  z-index: 2;
  font-size: 0.6rem;
}

.badge {
  padding: 0 10px;
  box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.2);
  border-radius: 30px;
  font-weight: bold;
}

.badge-title {
  top: 5px;
  left: 5px;
  background-color: #f50057;
  z-index: 2;
  font-size: 0.7rem;
}

.badge-price {
  top: 45px;
  left: 5px;
  background-color: #333;
}

.badge-discount {
  bottom: 15px;
  right: 20px;
}

@media (max-width: 1200px) {
  .productsCarousel {
    justify-content: unset !important;
  }
}

.loader-container {
  width: 60px;
  display: flex;
  height: 60px;
  align-items: center;
  margin: 50px auto;
}

.loader-item {
  margin: 0 3px;
  height: 50%;
  width: 5px;
  transition: all 0.05 cubic-bezier(0.165, 0.84, 0.44, 1);
  animation: growItem linear 0.4s infinite;
  border-radius: 8px;
}

.loader-container .loader-item:nth-child(1) {
  animation-delay: 0.1s;
}
.loader-container .loader-item:nth-child(2) {
  animation-delay: 0.15s;
}
.loader-container .loader-item:nth-child(3) {
  animation-delay: 0.2s;
}
.loader-container .loader-item:nth-child(4) {
  animation-delay: 0.25s;
}

.loader-container .loader-item:nth-child(5) {
  animation-delay: 0.3s;
}

@keyframes growItem {
  from {
    height: 50%;
  }
  to {
    height: 90%;
  }
}

.product-listing-container {
  display: flex;
}
@media (max-width: 760px) {
  .product-listing-container {
    flex-direction: column !important;
  }
}

.credentials-form-container {
  max-width: 400px;
  width: 100%;
  padding: 1.5em;
  background-color: white;
  border-radius: 8px;
  box-shadow: 6px 4px 8px 1px rgba(0, 0, 0, 0.15);
  justify-content: center;
}

.credentials-form-wrapper {
  /* this class already has set the display:flex property and flex-direction:column */
  justify-content: center;
  align-items: center;
}

.credentials-form-title {
  font-size: 1.8rem;
  font-family: "Noto Sans JP", sans-serif;
  opacity: 0.75;
}

#login-username,
#login-password {
  margin: 1rem 0;
}

.shoppingHistory-page {
  overflow-x: hidden;
  height: calc(100vh - 120px);
  overflow-y: auto;
}

.date-card-container {
  padding: 1rem 3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 2px solid grey;
  margin: 2rem 0;
  flex-wrap: wrap;
  text-align: center;
}

.full-width {
  width: 100%;
}

.date-card-row {
  color: rgba(0, 0, 0, 0.6) !important;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 120%;
}

.date-card-row > *,
.date-card-text {
  margin: 5px 0rem;
  font-size: 0.9rem;
}

.page {
  margin: 2rem 0;
  overflow-y: scroll;
}

.add-product-form {
  width: 400px;
  padding: 2rem;
  background-color: whitesmoke;
  border-radius: 1rem;
  overflow-y: scroll;
  text-align: center;
  margin: 5rem auto;
  position: relative !important;
}

.form-content {
  display: flex;
  flex-direction: column;
  padding: 2rem;
}

.form-close-btn {
  position: absolute;
  top: 10px;
  right: -40%;
}

.form-content > * {
  margin: 0.5rem 0;
}

.product-form-textarea {
  margin-top: 2.5rem;
}

.add-product-title {
  font-size: 1.5rem;
  color: #ee2c56;
  text-decoration: underline;
  margin-bottom: 0;
}

.add-product-divider {
  display: flex;
  justify-content: space-between;
}

.add-product-divider > * {
  margin: 0 10px;
  max-width: 40%;
}

.flex {
  display: flex;
}

.d-column {
  flex-direction: column;
}

.justify-between {
  justify-content: space-between;
}

.justify-center {
  justify-content: center;
}

.admin-buttons-container {
  max-width: 300px;
  margin: 0 auto;
}

.admin-buttons-container > * {
  margin: 1rem 0;
}

@media (max-width: 600px) {
  .add-product-form {
    width: 95vw;
    padding: 0;
    height: 100vh;
    margin: 2rem auto;
  }
}

.edit-products-page {
  display: flex;
  align-items: center;
}

.edit-product-item-container {
  width: 90%;
  max-width: 1000px;
  display: flex;
  padding: 0 1rem;
  cursor: pointer;
  transition: 0.3s all cubic-bezier(0.075, 0.82, 0.165, 1);
}

.edit-product-item-container:hover,
.edit-product-item-container:active {
  transform: translateY(-5px);
  box-shadow: 4px 4px 6px rgba(0, 0, 0, 0.2);
}

.edit-product-item-container:active,
.edit-product-item-container:focus,
.edit-product-item-summary:focus,
.edit-product-item-summary:active {
  outline: none;
  border: none;
}

.edit-product-item-summary {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1rem 0.5rem;
  transition: 0.3s all cubic-bezier(0.075, 0.82, 0.165, 1);
}

.edit-products-textField {
  margin: 0 auto;
  max-width: 500px;
  padding: 1.5rem;
}

.edit-products-textField > .textField-item {
  margin: 0.5rem 0;
}

details summary::-webkit-details-marker {
  /* Surprisingly, this also works for firefox*/
  display: none;
}

.edit-product-item-container:nth-child(odd) {
  background-color: rgba(0, 0, 0, 0.075);
}

.edit-product-icons {
  display: flex;
}

.edit-product-icons > .icon:hover {
  opacity: 0.8;
  cursor: pointer;
  transform: scale(1.1);
}

.edit-product-icons > .edit-icon {
  color: #33a2e2;
}

.edit-product-icons > .remove-icon {
  color: #e63763;
}

.notInStock {
  text-decoration: line-through;
  opacity: 0.4;
}

.floatBigAndSmall {
  transition: transform 0.2s ease-in-out;
  animation: float 5s infinite;
}

@keyframes float {
  0% {
    transform: translateY(-10px) scale(1.05);
  }
  50% {
    transform: translateY(0) scale(1);
  }
  100% {
    transform: translateY(-10px) scale(1.05);
  }
}

